import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheck } from "@fortawesome/free-solid-svg-icons";
import { faXmark } from "@fortawesome/free-solid-svg-icons";
import AdminCanteenContext from "../06Services/AdminCanteenContext";
import defaultImage from "../../images/defaultImg.jpg";
import ReactTooltip from 'react-tooltip';
import apiEndpoint from "../06Services/endpoint";
import axios from 'axios';

const AdminAddMenu = () => {
    const { canteenState } = React.useContext(AdminCanteenContext);
    // const { setCanteenState } = React.useContext(AdminCanteenContext);
    // const history = useHistory();

    const [canteenFullData, setCanteenFullData] = useState({
        id: '',
        name: '',
        address: '',
        image: '',
        menuName: ''
    })

    const [canteenForTable, setcanteenForTable] = useState({
        id: '',
        name: '',
        address: '',
        menuName: ''
    })

    const [getMenu, setGetMenu] = useState(false);

    const [menuData, setMenuData] = useState({
        menuName: '',
        dishName: '',
        description: '',
        drawMenu: false
    });

    const [infoValid, setInfoValid] = useState({
        menuName: true,
        dishName: true,
        description: true
    });

    const handleChange = (event) => {
        validateField(event);
        setMenuData({
            ...menuData,
            [event.target.name]: event.target.value,
        });
    };

    useEffect(() => {


        console.log("useEffect context canteenState output: ", canteenState)
    },
        [canteenState]
    )


    const validateField = (event) => {
        const target = event.target;

        if (target.id === "description") {
            if (!event.target.value.match(/^[A-ZĄ-Ž]{1}[\S\s]{1,255}$/)) {
                target.setCustomValidity(
                    "Aprašymas turi prasidėti didžiąja raide, būti nuo 2 iki 64 simbolių ir negali prasidėti tarpu"
                );
                setInfoValid({ ...infoValid, description: false });
            }
            else {
                target.setCustomValidity("");
                setInfoValid({ ...infoValid, description: true });
            }
        }

        if (target.validity.valueMissing) {
            target.setCustomValidity("Būtina užpildyti šį laukelį");
        } else
            if (target.validity.patternMismatch) {
                if (target.id === "id_name") {
                    target.setCustomValidity(
                        "Pavadinimas turi prasidėti didžiąja raide, būti nuo 2 iki 64 simbolių ir negali prasidėti tarpu"
                    );
                    setInfoValid({ ...infoValid, menuName: false });
                }
                else if (target.id === "id_dishName") {
                    target.setCustomValidity(
                        "Pavadinimas turi prasidėti didžiąja raide, būti nuo 2 iki 64 simbolių ir negali prasidėti tarpu"
                    );
                    setInfoValid({ ...infoValid, dishName: false });
                }
                return false;
            } else {
                if (target.id === "id_name") {
                    target.setCustomValidity("");
                    setInfoValid({ ...infoValid, menuName: true });
                }
                else if (target.id === "id_dishName") {
                    target.setCustomValidity("");
                    setInfoValid({ ...infoValid, dishName: true });
                }
                return true;
            }
    };

    function checkAllFields() {
        let allOk = false;
        if (
            menuData.menuName.length > 0 && infoValid.menuName
        ) {
            allOk = true;
        }
        return allOk;
    }

    const handleMenuSubmit = (event) => {
        event.preventDefault();
        let id = null;
        canteenState.map(item => id = item.id);
        let data = {
            canteenId: id,
            name: menuData.menuName
        }
        //console.log("Menu Submit json canteen id: %d", data.canteenId)
        axios.post(`${apiEndpoint}/api/menu/new`, data)
            .then(response => {
                // console.log("jsonBodyData: ", JSON.stringify(data));
                // console.log('response data: ', response.data);
                // console.log('Status: ', response.status);
                if (response.status === 201) {
                    // window.location.reload(); 
                    getCanteenEntity(data.canteenId);
                }
            })
            .catch(err => {
                console.error(err.data)
            });
    }

    const getCanteenEntity = async (id) => {
        console.log("Starting getCanteenEntity with id: %d", id)

        try {
            const request = await axios.get(`${apiEndpoint}/api/istaigos/canteen/${id}`);

            const data = request.data;

            console.log("response.data: ", data)

            setMenuData({ ...menuData, menuName: data.menu.name })

            setCanteenFullData({
                ...canteenFullData,
                id: data.id,
                name: data.name,
                address: data.address,
                image: data.image,
                menuName: data.menu.name
            })

            setcanteenForTable({
                ...canteenForTable,
                id: data.id,
                name: data.name,
                address: data.address,
                menuName: data.menu.name,
            })
            // console.log("canteenForTable: ", canteenForTable);
            setGetMenu(true);
        }

        catch (error) {
            console.error(error);
        }
    }

    function addMenu(entity) {

        return (
            <>
                <div className='row'>
                    <form className="col-sm-4 col-md-4 col-lg-4 py-3" onSubmit={handleMenuSubmit}>
                        <h4>Įdėkite meniu maitinimo įstaigai - {entity.map(item => item.name)}</h4>
                        <div className="form-group" style={{ position: 'relative' }}>
                            <label htmlFor="name">
                                Meniu pavadinimas <span className="fieldRequired">*</span>
                            </label>
                            <p data-for='name' data-tip='tooltip'>
                                <input
                                    type="text"
                                    className="form-control mt-2"
                                    name="menuName"
                                    id="id_name"
                                    value={menuData.menuName}
                                    onChange={handleChange}
                                    onInvalid={validateField}
                                    style={
                                        menuData.menuName.length > 0 ? infoValid.menuName ? { border: "1px solid lightgray" }
                                            : { border: "2px solid red" }
                                            : { border: "1px solid lightgray" }
                                    }
                                    required
                                    pattern="^[A-ZĄ-Ž]{1}[\S\s]{1,64}$"
                                    maxLength={64}
                                    data-toggle="tooltip"
                                    data-placement="top"
                                    title="Įveskite meniu pavadinimą"
                                />
                            </p>
                            {menuData.menuName.length === 0 || infoValid.menuName ? <span>&nbsp;</span> :
                                <ReactTooltip id='name' effect='solid' place='bottom' type='warning'>
                                    <b>Meniu pavadinimas turi prasidėti didžiąja raide, būti nuo 2 iki 64 simbolių ir negali prasidėti tarpu</b>
                                </ReactTooltip>}
                            {menuData.menuName.length > 0 ? infoValid.menuName ? <span className="approvemsg"><FontAwesomeIcon icon={faCheck} /></span>
                                : <span className="warningmsg"><FontAwesomeIcon icon={faXmark} /></span>
                                : <span className="approvemsg"></span>}
                            <br></br>
                            <button
                                type="submit"
                                className="btn btn-outline-primary form-group float-end "
                                id="btnCheckCanteenField"
                                disabled={!checkAllFields()}
                            >
                                Submit
                            </button>
                        </div>

                    </form>


                </div>
            </>

        )
    }

    function removeMenu(canteenForTable) {
        return (
            <>
                <div className='row'>
                    
                    <form className="col-sm-4 col-md-4 col-lg-4 py-3">
                        <h4>Matinimo įstaiga - {canteenForTable.name}
                        </h4>
                        <h4>Meniu pavadinimas - {canteenForTable.menuName}
                        </h4>
                        <div className="form-group" style={{ position: 'relative' }}>

                            <button
                                type="submit"
                                className="btn btn-outline-danger form-group float-end "
                                id="btnDeletemenu"
                                onClick={() => pressToDelete(canteenForTable.id)}
                            >
                                Delete
                            </button>
                        </div>
                    </form>
                </div>
            </>
        )
    }

    function pressToDelete(id) {
        id.preventDefault();
        axios.delete(`${apiEndpoint}/api/menu/delete/${id}`)
            .then(response => {
                if (response.status === 200) {
                    console.log("deleted: ", id)
                    setGetMenu(false);
                    setMenuData({ ...menuData, menuName: ''});
                    setCanteenFullData({
                        ...canteenForTable,
                        menuName: ''
                    });
                    setcanteenForTable({
                        ...canteenForTable,
                        menuName: ''
                    });
                }
            })
            .catch(err => {
                console.error(err.data)
            });
    }

    function pressToDraw() {
        // console.log("Canteen ID: %d", id);

        if (menuData.drawMenu) {

            setMenuData({ ...menuData, drawMenu: false })
        }
        else {
            setMenuData({ ...menuData, drawMenu: true })
        }
    }

    function pressToAddDish() {
        return (
            <div className='row'>
                <div className='col-sm-8 col-md-8 col-lg-8 py-3'>
                    <label style={{ paddingLeft: "4rem" }} htmlFor="dishName">
                        Patiekalas <span className="fieldRequired">*</span>
                    </label>
                    <p data-for='dish' data-tip='tooltip'>
                        <input
                            type="text"
                            className="form-control mt-2"
                            name="dishName"
                            id="id_dishName"
                            value={menuData.dishName}
                            onChange={handleChange}
                            onInvalid={validateField}
                            style={
                                menuData.dishName.length > 0 ? infoValid.dishName ? { border: "1px solid lightgray", marginLeft: '4rem', width: '47%' }
                                    : { border: "2px solid red", marginLeft: '4rem', width: '47%' }
                                    : { border: "1px solid lightgray", marginLeft: '4rem', width: '47%' }
                            }
                            required
                            pattern="^[A-ZĄ-Ž]{1}[\S\s]{1,64}$"
                            maxLength={64}
                            data-toggle="tooltip"
                            data-placement="top"
                            title="Įveskite patiekalo pavadinimą"
                        />
                    </p>
                    {menuData.dishName.length === 0 || infoValid.dishName ? <span>&nbsp;</span> :
                        <ReactTooltip id='dish' effect='solid' place='bottom' type='warning'>
                            <b>Patiekalo pavadinimas turi prasidėti didžiąja raide, būti nuo 2 iki 64 simbolių ir negali prasidėti tarpu</b>
                        </ReactTooltip>}
                    {menuData.dishName.length > 0 ? infoValid.dishName ? <span className="dishMsgTrue"><FontAwesomeIcon icon={faCheck} /></span>
                        : <span className="dishMsgFalse"><FontAwesomeIcon icon={faXmark} /></span>
                        : <span className="dishMsgTrue"></span>}
                    <br />
                    <label htmlFor="description" style={{ paddingLeft: "4rem" }}>
                        Aprašymas <span className="fieldRequired">*</span>
                    </label>
                    <p data-for='desc' data-tip='tooltip'>
                        <textarea
                            type="text"
                            id="description"
                            className="form-control mt-2"
                            name="description"
                            rows="4"
                            cols="25"
                            value={menuData.description}
                            onChange={handleChange}
                            style={
                                menuData.description.length > 0 ? infoValid.description ? { border: "1px solid lightgray", marginLeft: '4rem', width: '55%' }
                                    : { border: "2px solid red", marginLeft: '4rem', width: '55%' }
                                    : { border: "1px solid lightgray", marginLeft: '4rem', width: '55%' }
                            }
                            required
                            maxLength={255}
                            data-toggle="tooltip"
                            data-placement="top"
                            title="Aprašykite patiekalą"
                        />
                    </p>
                    {menuData.description.length === 0 || infoValid.description ? <span>&nbsp;</span> :
                        <ReactTooltip id='desc' effect='solid' place='bottom' type='warning'>
                            <b>Aprašymas turi prasidėti didžiąja raide, būti nuo 2 iki 64 simbolių ir negali prasidėti tarpu</b>
                        </ReactTooltip>}
                    {menuData.description.length > 0 ? infoValid.description ? <span className="descMsgTrue"><FontAwesomeIcon icon={faCheck} /></span>
                        : <span className="descMsgFalse"><FontAwesomeIcon icon={faXmark} /></span>
                        : <span className="descMsgTrue"></span>}
                </div>
            </div>
        )
    }

    function pressToRemoveDish() {

    }

    return (
        <div className="container pt-4">
            <div className="row">
                <div className="col-sm-9 col-md-6 col-lg-9">
                    <h3>Pridėti meniu</h3>
                    <table className="table">
                        <thead>
                            <tr key='id'>
                                <th key='id' scope="col">Įmonės kodas</th>
                                <th key='name' scope="col">Pavadinimas</th>
                                <th key='address' scope="col">Adresas</th>
                                <th key='menu' scope="col">Meniu</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                getMenu === false ?
                                    canteenState.map(item =>
                                        <tr key={item.id}>
                                            <td>{item.id}</td>
                                            <td>{item.name}</td>
                                            <td>{item.address}</td>
                                            <td><button
                                                onClick={() => pressToDraw()}
                                                id="btnAddmenu"
                                                className="btn btn-secondary btn-sm btn-block"
                                            >Pridėti Meniu...</button></td>
                                        </tr>
                                    )
                                    :
                                    <tr key='id'>
                                        {Object.keys(canteenForTable).map((item) => {
                                            return <td>{canteenForTable[item]}</td>
                                        }
                                        )}
                                    </tr>
                            }
                        </tbody>
                    </table>

                    {/* {getMenu === true ? removeMenu(canteenForTable) : menuData.drawMenu === true ? addMenu(canteenState) : <span>&nbsp;</span>} */}
                    {
                        getMenu === false ? addMenu(canteenState) : removeMenu(canteenForTable)
                    }


                </div>

                <div className="col-sm-3 col-md-6 col-lg-3" style={{ backgroundColor: "#eeeeee", textAlign: "center" }}>
                    <h4>Maitinimo įstaigos atvaizdavimas klientui:</h4>
                    <br />
                    {
                        getMenu === false ?
                            canteenState.map(item =>
                                <div key={item.id} className='card' style={{ width: '100%' }}>
                                    <img src={item.image !== null ? `data:image/*;base64,${item.image}`
                                        : defaultImage} className="card-img-top" alt="Failed To Load"></img>
                                    <div className="card-body">
                                        <h5 className="card-title">{item.name}</h5>
                                        <p className="card-text">{item.address}</p>
                                        <p className="card-text">{item.menu}</p>
                                    </div>
                                </div>
                            )
                            :
                            <div key={552} className='card' style={{ width: '100%' }}>
                                <img src={canteenFullData.image !== null ? `data:image/*;base64,${canteenFullData.image}`
                                    : defaultImage} className="card-img-top" alt="Failed To Load"></img>
                                <div className="card-body">
                                    <h5 className="card-title">{canteenFullData.name}</h5>
                                    <p className="card-text">{canteenFullData.address}</p>
                                    <p className="card-text">{canteenFullData.menuName}</p>
                                </div>
                            </div>
                    }
                </div>
            </div>

            {/* {canteenState === null ? <p>null</p> : console.log(canteenState)} */}
            {/* <div className='row'>{canteenState.map(item => item.id)}</div> */}
            <div className="row pt-4"></div>
            <Link
                className="text-decoration-none"
                type="button"
                to={`/canteen`}
            >
                <button className="btn btn-outline-primary">Atgal</button>
            </Link>
        </div>

    );
}

export default AdminAddMenu;